
import MyHeader from '../base/mytop'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

export default {
name: "link-pay",
components: {
XHeader,
MyHeader,
Scroller,
Group,
Cell,
CellBox
},
data(){
return{
datalist:[
{
"id": 1,
"label": "",
"sponsor": "",
"sum": 45,
"type": ""
},
],
pullupConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},
pulldownConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
}
}
},
methods:{
goSearch(){
this.$router.push({ path:'/payinfo'})
},
loadMoreData(){},
refresh(){}
},
beforeRouteEnter:function(to,from,next){
next(function(vm){
vm.axios.get('http://rap2api.taobao.org/app/mock/22289/getpayinfo').then((res)=>{
console.log(res.data)
vm.datalist = res.data["payinfo"]
})
})
},



}



